import axios from "axios";

export const getPlayers = () => {
  return axios({
    url: `/api/users/players?user_name=mario123`,
    method: "GET",
  }).then((res) => res);
};

export const oktaLogIn = (user) => {
  return axios({
    url: `/api/users/aut`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: {
      userName: user,
      email: "user@mail.com",
      empNumber: 0,
    },
  }).then((res) => res);
};

export const adminLogIn = (user, password) => {
  return axios({
    url: `/api/users/autadmin`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: {
      userName: user,
      password: password,
    },
  }).then((res) => res);
};
